import { Button, Color, Component, Node, Sprite, SpriteFrame, _decorator } from "cc";
import { AS } from "db://as_framework/scripts//ASComponent";
import { UIDrag } from "db://as_framework/scripts/UIDrag";
const { ccclass, property, requireComponent } = _decorator;

@ccclass("Swap")
export class MCQ extends AS(Component) {
  @property({ type: SpriteFrame }) private parentImage: SpriteFrame[] = [];
  @property({ type: SpriteFrame }) private dragImage: SpriteFrame[] = [];
  @property({ type: Node }) private DragContainer: Node;
  @property({ type: Node }) private ParentImageContainer: Node;
  @property({ type: Node }) private check: Node;
  @property({ type: Number }) private Answer: number[] = [];

  awake() {
    this._updateComponent();
    this.check.on(Button.EventType.CLICK, this._check);
  }

  _updateComponent() {
    for (let i = 0; i < this.parentImage.length; i++) {
      this.DragContainer.children[i].addComponent(Sprite).spriteFrame = this.dragImage[i];
      this.DragContainer.children[i].addComponent(UIDrag);
      this.ParentImageContainer.children[i].addComponent(Sprite).spriteFrame = this.parentImage[i];
    }
  }
  _check() {
    const dragables = this.DragContainer.getComponentsInChildren(UIDrag);

    dragables.forEach((dragable, i) => {
      if (this.Answer.toString() == this.DragContainer.children[i].name) {
        this._feedback(1, dragable);
      } else this._feedback(2, dragable);
    });
  }
  _feedback(feedtype, dragable) {
    const colorarray = [Color.GREEN, Color.RED];
    dragable.node.getComponent(Sprite).color = colorarray[feedtype];
  }
}
